<template>
  <div class="table">
    <div class="dealer">
      <div
        class="flip-card"
        :class="{dealerflip: flipcard}"
        v-for="(card, index) of dealerCards"
        v-bind:key="index"
      >
        <div class="flip-card-inner">
          <div class="flip-card-front">
            <img :src="require('../assets/SVGcards/' + `${card.card}`)" alt="Avatar" />
          </div>
          <div class="flip-card-back"></div>
        </div>
      </div>
      <div v-show="!flipcard" class="dealer-count">Счет крупье: {{ dealerCount}}</div>
    </div>
    <div class="result" v-show="playerWin">Вы победили</div>
    <div class="result" v-show="dealerWin">Крупье выиграл</div>
    <div class="result" v-show="draw">Ничья</div>
    <div class="player">
      <div class="player-count">Счет игрока: {{playerCount}}</div>
      <img
        class="carding"
        v-for="(card, index) of playerCards"
        v-bind:key="index"
        :src="require('../assets/SVGcards/' + `${card.card}`)"
      />
    </div>

    <div class="buttons">
      <button @click.prevent="startPlay">Начать игру</button>
      <button @click="moreCard" :disabled="(playerCount>=21)">Еще карту</button>
      <button @click="dealerPlay">Стоп</button>
    </div>
  </div>
</template>

<script>
let reqSvgs = require.context("@/assets/SVGcards/", true, /\.svg$/);
let cardsSVG = reqSvgs.keys();
let newarr = [];
cardsSVG.map(function (item) {
  item = item.replace(/\.\//, "");
  let value = item.match(/^\d./);
  if (value) {
    let newitem = { valueCard: value.toString(), card: item };
    newarr.push(newitem);
  } else if (item.match(/^ace/)) {
    let newitem = { valueCard: 11, card: item };
    newarr.push(newitem);
  } else {
    let newitem = { valueCard: 10, card: item };
    newarr.push(newitem);
  }
});
let fullDeck = newarr.slice();
console.log(fullDeck);
export default {
  name: "Blackjack",
  data() {
    return {
      cards: fullDeck,
      dealerCards: [],
      playerCards: [],
      dealerCount: 0,
      playerCount: 0,
      playerWin: false,
      dealerWin: false,
      draw: false,
      flipcard: true,
    };
  },

  methods: {
    refreshDealerCards() {
      this.dealerCount = 0;
      this.dealerCards.forEach((element) => {
        this.dealerCount += Number.parseInt(element.valueCard);
      });
    },
    refreshPlayerCards() {
      this.playerCount = 0;
      this.playerCards.forEach((element) => {
        this.playerCount += Number.parseInt(element.valueCard);
      });
    },
    randomCards() {
      let min = 0;
      let max = this.cards.length - 1;
      let rand = min - 0.5 + Math.random() * (max - min + 1);
      return Math.round(rand);
    },
    newGame() {
      this.cards = newarr.slice();
      this.dealerCards = [];
      this.playerCards = [];
      this.dealerCount = 0;
      this.playerCount = 0;
      this.playerWin = false;
      this.dealerWin = false;
      this.draw = false;
      this.flipcard = true;
    },
    startPlay() {
      this.newGame();
      while (this.dealerCards.length < 2 && this.playerCards.length < 2) {
        let randomNum = this.randomCards();
        this.dealerCards.push(this.cards[randomNum]);
        this.cards.splice(randomNum, 1);
        this.refreshDealerCards();
        randomNum = this.randomCards();
        this.playerCards.push(this.cards[randomNum]);
        this.cards.splice(randomNum, 1);
        this.refreshPlayerCards();
      }
    },
    moreCard() {
      let randomNum = this.randomCards();
      this.playerCards.push(this.cards[randomNum]);
      this.cards.splice(randomNum, 1);
      this.refreshPlayerCards();
    },

    async dealerPlay() {
      await setTimeout(() => {
        while (this.dealerCount < this.playerCount || this.dealerCount < 16) {
          if (this.playerCount > 21) break;
          let randomNum = this.randomCards();
          this.dealerCards.push(this.cards[randomNum]);
          this.cards.splice(randomNum, 1);
          this.refreshDealerCards();
          this.flipcard = false;
        }
      }, 1000);

      this.flipcard = false;
      this.checkWiner();
    },

    checkWiner() {
      if (
        (this.playerCount > 21 && this.dealerCount <= 21) ||
        (this.playerCount < this.dealerCount && this.dealerCount <= 21)
      ) {
        this.playerWin = false;
        this.dealerWin = true;
      } else if (this.dealerCount > 21 && this.playerCount <= 21) {
        this.dealerWin = false;
        this.playerWin = true;
      } else if (this.dealerCount == this.playerCount) this.draw = true;
    },
  },
};
</script>

<style lang='scss'>
body {
  margin: 0;
  padding: 0;
}
.table {
  display: flex;
  flex-direction: column;
  background: rgb(72, 117, 27);
  color: white;
  justify-content: space-between;
  min-height: 100vh;
}
.dealer,
.player {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  perspective: 600px;
  img {
    width: 100px;
    height: 100%;
    object-fit: contain;
  }
}
button {
  width: 150px;
  height: 50px;
}
.dealer-count,
.player-count {
  width: 100%;
  text-align: center;
  font-size: 20px;
}
.buttons {
  text-align: center;
  button {
    margin: 0 10px;
  }
}

.result {
  text-align: center;
  font-size: 50px;
}
.flip-card {
  background-color: transparent;
  width: 100px;
  height: 143px;
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flip-card.dealerflip .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-back {
  background: url("../assets/backface.png");
  background-size: contain;
  color: white;
  transform: rotateY(180deg);
}
</style>
